<template>
  <div class="admin-container">
    <h1>لوحة التحكم</h1>
    <ag-grid-vue
      :columnDefs="columnDefs"
      :rowData="requests"
      style="height: 500px"
      class="ag-theme-quartz"
      :enableRtl="true"
    >
    </ag-grid-vue>
  </div>
</template>

<script>
import "ag-grid-community/styles/ag-grid.css" // Mandatory CSS required by the grid
import "ag-grid-community/styles/ag-theme-quartz.css" // Optional Theme applied to the grid
import { AgGridVue } from "ag-grid-vue3" // AG Grid Component
import axios from "axios"
import qs from "qs"
import { serverUrl } from "../const"
export default {
  components: {
    AgGridVue,
  },
  data() {
    return {
      requests: [],
      columnDefs: [
        { field: "name", headerName: "الاسم", sortable: true },
        { field: "phoneNumber", headerName: "رقم الهاتف", sortable: true },
        {
          headerName: "التصرف",
          cellRenderer: (params) => {
            if (params.data?._id !== null) {
              const div = document.createElement("div")
              div.classList.add("btn-group")
              div.innerHTML = `
                  <button class="btn btn-primary">التقاط صورة</button>
            `
              this.addClickEventListener(div, params.data)
              return div
            } else {
              return ""
            }
          },
        },
        {
          headerName: "الصورة",
          cellRenderer: (params) => {
            if (params.data?.urlPicture) {
              const div = document.createElement("div")
              div.classList.add("btn-group")
              div.innerHTML = `
                  <a href="${serverUrl}/api/admin/picture/${params.data?._id}" target="_blank" class="btn btn-info">فتح الصورة</a>`
              return div
            } else {
              return ""
            }
          },
        },
      ],
    }
  },
  methods: {
    async takePhoto(data) {
      const idOrder = data?._id
      const phoneNumber = data?.phoneNumber

      try {
        const inputElement = document.createElement("input")
        inputElement.type = "file"
        inputElement.accept = "image/*"
        inputElement.capture = "camera" // Utiliser l'appareil photo du mobile
        inputElement.addEventListener("change", async (event) => {
          const file = event.target.files[0]
          if (file) {
            const reader = new FileReader()
            reader.readAsDataURL(file)
            reader.onload = async () => {
              const imageDataUrl = reader.result
              const response = await axios.post(
                `${serverUrl}/api/admin/uploadPhoto`,
                {
                  imageData: imageDataUrl,
                  orderId: idOrder,
                }
              )
              const imageUrl = response.data?.imageUrl
              await this.sendPicture(imageUrl, phoneNumber)
              //alert("Photo enregistrée avec succès !")
            }
          }
        })
        inputElement.click()
      } catch (error) {
        console.error("Erreur lors de la prise de la photo :", error)
      }
    },
    addClickEventListener(element, data) {
      element.addEventListener("click", () => this.takePhoto(data))
    },
    async fetchRequests() {
      const response = await axios.get(`${serverUrl}/api/admin/requests`)
      console.log("response", response)
      this.requests = response.data
    },
    async sendPicture(url, phoneNumber) {
      console.log("url", url)
      var data = qs.stringify({
        token: "j290mey56mbdrzo3",
        to: phoneNumber,
        image: url,
        caption: "اهداء",
      })
      console.log("phoneNumber", phoneNumber)
      var config = {
        method: "post",
        url: "https://api.ultramsg.com/instance81883/messages/image",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
        data: data,
      }

      axios(config)
        .then(function (response) {
          console.log(JSON.stringify(response.data))
          alert("تم التقاط الصورة وإرسالها إلى رقم الواتساب!")
        })
        .catch(function (error) {
          console.log(error)
        })
    },
  },
  mounted() {
    this.fetchRequests()
  },
}
</script>

<style scoped>
.admin-container {
  text-align: right;
  margin: 20px;
}

.btn-group {
  display: flex;
  gap: 10px;
}
</style>
